import {
  Component,
  Input
} from '@angular/core';
import {DomSanitizer} from "@angular/platform-browser";
import {InputBoolean} from "ng-zorro-antd";

export const KPI_COLORS = [
  '#2a7cff',
  '#00cda1',
  '#ffa234',
  '#a100cd',
  '#cc2229',
  '#009fe3',
  '#688cda',
  '#bec4cd'
];

export interface KPI {
  count: number;
  description: string;
}

@Component({
  selector: 'cmacs-kpi',
  exportAs: 'cmacsKpi',
  templateUrl: './cmacs-kpi.component.html',
  styleUrls: ['./cmacs-kpi.component.css'],
  styles: [
    `
      cmacs-kpi {
        display: block;
      }
    `
  ]
})
export class CmacsKpiComponent {

  @Input() data: KPI[];
  @Input() title: string;
  @Input() @InputBoolean() showTotalCount = false;

  constructor(private sanitizer: DomSanitizer) { }

  sanitizeStyle(style: string) {
    return this.sanitizer.bypassSecurityTrustStyle(style);
  }

  getTotalCount() {
    let total = 0;
    this.data.forEach((item) => {
      total += item.count;
    });
    return total;
  }

  getColoredData() {
    let coloredData: any = [];
    const remaining = this.data.length % KPI_COLORS.length;
    let rate = this.data.length / KPI_COLORS.length;
    if (remaining > 0) {
      rate = Math.trunc(rate) + 1;
    }

    let tempRate = rate;
    let opacity = 1;
    let colorIndex = 0;
    this.data.forEach((item) => {
      if (tempRate === 0) {
        tempRate = rate;
        colorIndex += 1;
        opacity = 1;
      }

      if (colorIndex >= KPI_COLORS.length) {
        colorIndex = 0;
      }

      if (opacity === 0.4) {
        opacity = 1;
      }

      coloredData.push({
        count: item.count,
        description: item.description,
        color: KPI_COLORS[colorIndex],
        opacity: opacity
      });

      opacity = opacity - 0.2;
      tempRate--;
    });

    return coloredData;
  }

}
